@import 'src/assets/css/veriables.scss';
.leftbar {
	width: $left-width;
	height: 100vh;
	position: fixed;
	left: 0;
	z-index: 999;
	overflow: auto;
	float: left;
	background-color: $light-green;
	text-align: center;
	.logo {
		width: 80%;
		margin: 10px 0;
		border-radius: 3px;
	}
	ul {
		padding: 0;
		li {
			list-style: none;
			height: $left-width;
			line-height: $left-width;
			color: #fff;
			cursor: pointer;
			overflow: hidden;
			position: relative;
			div {
				height: $left-width;
				line-height: $left-width;
				transition: .3s line-height;
			}
			p {
				width: 100%;
				text-align: center;
				font-size: 12px;
				height: 30px;
				line-height: 30px;
				position: absolute;
				top: $left-width;
				transition: .3s top;
			}
			&:hover, &.active {
				color: $color;
				background-color: #fff;
				div {
					height: 36px;
					line-height: 36px;
				}
				p {
					top: 30px;
				}
			}
			.anticon {
				font-size: 20px !important;
			}
		}
	}
}