/* 项目个性化样式 */
html, body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  color: $black;
  overflow: auto;
  box-sizing: border-box;
}

// 布局
html {
	padding-left: $left-width;
}
body {
	overflow: auto;
}

// 链接
a {
	color: $color;
	text-decoration: underline;
	&:hover {
		color: $dark-green;
		text-decoration: underline;
	} 
}
